/**
 * 
 */
class StateElement {
    constructor(machine, state, callback) {
        this.machine = machine;
        this.state = state;
        this.callback = callback;
    }
    execute(iteration) {
        if (this.callback) {
            this.callback(this, iteration);
        }
    }
}
/**
 * 
 */
class StateMachineCollection {
    constructor(machine) {
        this.machine = machine;
        this._elements = [];
    }
    get elements() {
        return this._elements;
    }
    add(el) {
        if (el) {
            if (Array.isArray(el)) {
                this.elements = this.elements.concat(el);
            } else {
                this.elements.push(el);
            }
            return this;
        } else {
            return this.elements;
        }
        return this;
    }
    clear() {
        this._elements = [];
    }
}

/**
 * 
 */
class EntityElement extends StateElement {
    constructor(machine, state, callback) {
        super(machine, state, callback);
        this._children = [];
    }
    get x() {
        return this.state.x;
    }
    get y() {
        return this.state.y;
    }
    get children() {
        return this._children;
    }
}

/**
 * 
 */
class StateOperator extends StateElement {
    execute(iteration, el) {
        if (this.callback) {
            this.callback(el, iteration);
        }
    }
}

/**
 * 
 */
class StateBuilder extends StateElement {

}

/**
 * 
 */
class StateMachine {
    constructor() {
        this.remIterations = 0;
        this.iteration = 0;
        this.maxIterations = 5;

        this.stateElements = new StateMachineCollection(this);
        this.stateOperators = new StateMachineCollection(this);
        this.stateBuilders = new StateMachineCollection(this);
    }
    get elements() {
        return this.stateElements;
    }
    get operators() {
        return this.stateOperators;
    }
    get builders() {
        return this.stateBuilders;
    }
    reset() {
        this.elements.clear();
        this.iteration = this.remIterations = 0;
        this.builders.elements.forEach((stateBu) => {
            stateBu.execute(0);
        })
    }
    run(iterations) {
        this.builders.elements.forEach((stateBu) => {
            stateBu.execute(0);
        });
        this.advance(iterations);
    }
    advance(iterations) {
        for (
            this.remIterations = iterations; this.remIterations >= 0 && this.iteration < this.maxIterations; this.remIterations = (this.remIterations !== -1 ? this.remIterations - 1 : -1)) {
            this.elements.elements.forEach((stateEl) => {
                this.operators.elements.forEach((stateOp) => {
                    stateOp.execute(this.iteration, stateEl);
                });
            });
            this.iteration++;
        }
    }
}


export { StateElement, StateMachineCollection, StateMachine, EntityElement, StateOperator, StateBuilder };